import { useEffect, useState } from "react";

import { ICardData, ICardError, IValidationResponse } from "../intefaces";
import { VALIDATION_MESSAGES } from "../store/vars";

import { checkDate, getInputStatus } from "./validation";

const useValidation = (card: ICardData): IValidationResponse => {
  const [errors, setErrors] = useState<ICardError>({});

  const [message, setMessage] = useState<string>("");

  useEffect(() => {
    const validate = () => {
      setErrors({});

      setMessage(getInputStatus(card.summary));

      if (getInputStatus(card.summary) !== VALIDATION_MESSAGES.ok) {
        setErrors(state => ({ ...state, summary: true }));
      }

      if (!checkDate(card.date)) {
        setErrors(state => ({ ...state, date: true }));
      }
    };

    validate();
  }, [card.date, card.summary]);

  return { errors, message };
};

export default useValidation;
