import React from "react";
import { IInputProps } from "../../../../intefaces";

const InputCheckbox = (props: IInputProps): JSX.Element => {
  const name = "isPromoted";

  const { value } = props;

  const handleCheck = (event: React.ChangeEvent<HTMLInputElement>) => {
    props.saveInput(event.target.checked, "isPromoted");
  };

  return (
    <div className="form__input-wrap">
      <label className="form__input-label label-checkbox" htmlFor="input-checkbox">
        Do you want to promote your message?
        <input
          onChange={handleCheck}
          className="form__checkbox"
          name={name}
          checked={value[name]}
          type="checkbox"
          id="input-checkbox"
        />
      </label>
    </div>
  );
};

export default InputCheckbox;
