import React from "react";
import { ICardData } from "../../../intefaces";
import Card from "./Card";

interface ICardsWrap {
  cardsData: ICardData[];
}

const CardsContainer = (props: ICardsWrap): JSX.Element => {
  const { cardsData } = props;

  return (
    <div className="cards">
      {cardsData.map(cardData => {
        return <Card data={cardData} key={JSON.stringify(cardData)} />;
      })}
    </div>
  );
};

export default CardsContainer;
