import React from "react";
import { IInputProps } from "../../../../intefaces";
import { options } from "../../../../store/vars";

import SelectOption from "./SelectOption";

const Select = (props: IInputProps): JSX.Element => {
  const name = "headline";

  const getSelectState = (e: React.ChangeEvent<HTMLSelectElement>) => {
    props.saveInput(e.target.value, e.target.name);
  };

  return (
    <div className="form__input-wrap">
      <label className="form__input-label" htmlFor="select">
        Deal type:
        <select onChange={getSelectState} className="form__input" name={name} id="select">
          {options.map(option => {
            return <SelectOption option={option} key={option.id} />;
          })}
        </select>
      </label>
    </div>
  );
};

export default Select;
