import React from "react";
import { IInputProps } from "../../../../intefaces";

const InputDate = (props: IInputProps): JSX.Element => {
  const name = "date";

  const { value, isErrors, isSubmitFail } = props;

  const setInput = (event: React.ChangeEvent<HTMLInputElement>) => {
    props.saveInput(event.target.value, event.target.name);
  };

  return (
    <div className="form__input-wrap">
      <label className="form__input-label" htmlFor="input-date">
        Publication date:
        <input
          onChange={setInput}
          value={value[name]}
          className="form__input"
          type="date"
          name={name}
          id="input-date"
          min="2021-01-01"
          max="2021-12-31"
        />
      </label>

      {isSubmitFail && isErrors && <p className="form__error-msg">Please, add correct date</p>}
    </div>
  );
};

export default InputDate;
