import React from "react";
import { ITextInputDataWrap } from "../../../../intefaces";

const InputText = (props: ITextInputDataWrap): JSX.Element => {
  const name = "summary";

  const { textData } = props;

  const { value, isErrors, isSubmitFail, message, saveInput } = textData;

  const setInput = (event: React.ChangeEvent<HTMLInputElement>) => {
    saveInput(event.target.value, event.target.name);
  };

  return (
    <div className="form__input-wrap">
      <label className="form__input-label" htmlFor="input-text">
        Advertisement text:
        <input
          onChange={setInput}
          value={value[name]}
          name={name}
          className="form__input"
          type="text"
          id="input-text"
        />
      </label>
      {isSubmitFail && isErrors && <p className="form__error-msg">{message}</p>}
    </div>
  );
};

export default InputText;
