import React, { useState } from "react";
import Header from "./components/Header/Header";
import Form from "./components/Form/Form";
import CardsContainer from "./components/Cards/CardsContatiner";
import { ICardData } from "../intefaces";

const Render = (): JSX.Element => {
  const [submitData, setSubmitData] = useState<ICardData[]>([]);

  const renderSubmitData = (data: ICardData) => {
    setSubmitData(prev => [data, ...prev]);
  };

  return (
    <>
      <Header />
      <div className="working-zone">
        <Form saveData={renderSubmitData} />
        <CardsContainer cardsData={submitData} />
      </div>
    </>
  );
};

export default Render;
