import React from "react";
import { IInputProps, IRadio } from "../../../../intefaces";
import InputRadio from "./InputRadio";

const InputRadioWrap = (props: IInputProps): JSX.Element => {
  const radioName = "location";

  const labels: string[] = ["Europe", "USA"];

  return (
    <div className="form__input-wrap">
      <p className="form__input-label">Your Location:</p>
      <div className="form__input-radio-wrap">
        {labels.map(label => {
          const labelData: IRadio = {
            labelText: label,
            name: radioName,
          };
          return <InputRadio radioData={labelData} callback={props} key={labelData.labelText} />;
        })}
      </div>
    </div>
  );
};

export default InputRadioWrap;
