import { VALIDATION_MESSAGES, maxInputLength, regexName, regexASCIISymbols } from "../store/vars";

export const getInputStatus = (text: string): string => {
  const isInputTooLong = text.length > maxInputLength;

  const isInputEmpty = !text.length;

  const isInputPassValidation = regexName.test(text);

  const isASCIISymbolInInput = regexASCIISymbols.test(text);

  switch (true) {
    case isInputEmpty:
      return VALIDATION_MESSAGES.empty;
      break;
    case isInputTooLong:
      return VALIDATION_MESSAGES.tooLong;
      break;
    case !isInputPassValidation:
      return VALIDATION_MESSAGES.onlyNumbers;
      break;
    case isASCIISymbolInInput:
      return VALIDATION_MESSAGES.asciiSymbols;
      break;
    default:
      return VALIDATION_MESSAGES.ok;
  }
};

export const checkDate = (date: string): boolean => {
  const dateParts = date.split("-");

  if (!dateParts.length || dateParts.length !== 3) return false;

  const [year, month, day]: string[] = dateParts;

  const isYearOk = +year === 2021;

  const isMonthOk = +month >= 1 && +month <= 12;

  const monthLength = [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31];

  const isDayOk = +day >= 1 && +day <= monthLength[+month - 1];

  return isYearOk && isMonthOk && isDayOk;
};
