import React from "react";
import { IFormDataWrap, IInputProps } from "../../../intefaces";

import InputCheckbox from "./components/InputCheckbox";
import InputDate from "./components/InputDate";
import InputRadioWrap from "./components/InputRadioWrap";
import InputText from "./components/InputText";
import Select from "./components/Select";
import SubmitButton from "./components/SubmitButton";

import { VALIDATION_MESSAGES } from "../../../store/vars";

const FormView = (props: IFormDataWrap): JSX.Element => {
  const { data } = props;

  const { handleSubmit, addSubmitData, card, errors, isSubmitFail, isSubmitPass, message } = data;

  const isErrors = !!errors.summary;

  const inputTextData: IInputProps = { saveInput: addSubmitData, value: card, isErrors, isSubmitFail, message };

  return (
    <form onSubmit={handleSubmit} className="form">
      <InputText textData={inputTextData} />
      <InputDate value={card} saveInput={addSubmitData} isErrors={errors.date} isSubmitFail={isSubmitFail} />
      <Select value={card} saveInput={addSubmitData} />
      <InputCheckbox value={card} saveInput={addSubmitData} />
      <InputRadioWrap value={card} saveInput={addSubmitData} />
      <SubmitButton />
      {isSubmitPass && <p className="form__success-msg">{VALIDATION_MESSAGES.ok}</p>}
    </form>
  );
};

export default FormView;
