import React from "react";
import { ICardData } from "../../../intefaces";

interface IDataWrap {
  data: ICardData;
}

const Card = (props: IDataWrap): JSX.Element => {
  const { data } = props;

  const { headline, location, summary, date, isPromoted } = data;

  const classes = isPromoted ? "cards__card cards__card_promoted" : "cards__card";

  return (
    <a rel="noreferrer" target="_blank" href="https://rs.school/index.html" className={classes}>
      <h2 className="cards__card__headline">{headline}</h2>
      <p className="cards__card__link">{location}</p>
      <p>{summary}</p>
      <p>{date}</p>
    </a>
  );
};

export default Card;
