import React, { FormEvent, useState } from "react";

import { ICardData, IFormData, IFormProps } from "../../../intefaces";
import { newCard } from "../../../store/vars";

import useValidation from "../../../Logic/useValidation";

import FormView from "./FormView";

const Form = (props: IFormProps): JSX.Element => {
  const [isSubmitFail, setIsSubmitFail] = useState<boolean>(false);

  const [isSubmitPass, setIsSubmitPass] = useState<boolean>(false);

  const [card, setCard] = useState<ICardData>({ ...newCard });

  const { errors, message } = useValidation(card);

  const handleSubmit = (event: FormEvent<HTMLFormElement>) => {
    event.preventDefault();

    if (!Object.keys(errors).length) {
      props.saveData(card);

      setCard({ ...newCard });

      setIsSubmitFail(false);

      setIsSubmitPass(true);

      setTimeout(() => {
        setIsSubmitPass(false);
      }, 1000);
    } else {
      setIsSubmitFail(true);
    }
  };

  const addSubmitData = (text: string | boolean, type: string) => {
    card[type] = text;

    setCard({ ...card });
  };

  const formData: IFormData = { handleSubmit, addSubmitData, errors, card, isSubmitPass, isSubmitFail, message };

  return <FormView data={formData} />;
};

export default Form;
